        article: "It was Universal week at the holiday box office, with two new films from the studio and a third from last week still leading the way. Only one of them is currently available on streaming too. Certainly there is some focus on the big franchise crossing a milestone in pandemic-record time, but the more hopeful story may be the studios acquired franchise showing that family films could be on the rise. Or that nobody realized it was also available for free on Peacock.           It was definitely a family kind of weekend all around, with BBQs, fireworks, and the Toretto gang holding on to its #1 spot at the box office, though it was a good news/bad news situation. Starting with the obvious good is that F9 cleared $100 million in just eight days. Thats a full week better than Paramounts A Quiet Place Part II and about 10 weeks faster than it took Godzilla vs. Kong. Exclusive company to be sure, though F9 would certainly like to join the ranks of its predecessors and set new milestones for the box office going forward.    Making $24 million this weekend is almost $5 million better than A Quiet Place Part IIs second go- round.The big difference is that Quiet debuted on a holiday weekend with $47.5 million and fell to $19.2 million.If F9s $70 million start last week was even more impressive, then we must also conclude that barely beating Quiet in week two with a Monday holiday is less so.Nearly $126 million through Monday is still nothing to sneeze at during these times but the 10 - day total of $117 million is putting F9 further on course with the fourth entry, Fast & Furious, than any films since. (Hobbs & Shaw excluded.) The reunion of Vin Diesel & Paul Walker in that fourth film had grossed $116.4 million by its tenth day, the result of a $27.2 million second(non - holiday) weekend.F9 does have the summer weekday advantage in its favor(F & F opened in April) which could be the difference in getting over the $155 million that #4 grossed.However, anything under $176 million would still be a $50 + million drop for The Fate of the Furious final gross.Still, F9 has become the highest - grossing film worldwide during the pandemic with over $491 million.",
